.brdWrap {
  background-color: #fff;
}

.brdWrap + .brdWrap {
  margin-top: 3.5rem;
}

.brdWrap.none + .brdWrap {
  margin-top: 0;
}

.brdWrap + .brdWrap .brdListTop {
  margin-top: 0;
}

.innerCont > .brdWrap,
.section > .brdWrap {
  margin-left: -1.6rem;
  margin-right: -1.6rem;
}

.brdWrap > .btnArea {
  padding-left: 1.6rem;
  padding-right: 1.6rem;
}

.brdWrap .tipBox {
  margin: 0;
}

.brdWrap .paging + .tipBox {
  margin-top: 6.5rem;
}

.brdWrap .history {
  margin-top: 3rem;
}

.popupCont .brdWrap {
  padding-bottom: 5.6rem;
}

.popupCont .innerCont > .brdWrap {
  padding-bottom: 0;
}

.brdWrap.coaching {
  margin-top: 3rem;
  background: transparent;
}

.brdWrap.coaching .brdList .info {
  height: auto;
  padding-left: 0;
}

.brdWrap.coaching .brdList .info .tit {
  font-size: 1.6rem;
}

.brdWrap.coaching .brdList .info .writer {
  margin-top: 0.5624rem;
}

.brdWrap.coaching .brdList li.new .info .writer {
  position: relative;
  padding-left: 2.7rem;
}

.brdWrap.coaching .brdList li.new .info .writer:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0.1rem;
  width: 1.6rem;
  height: 1.6rem;
  border-radius: 50%;
  /* background: url("../../images/mobile/contents/ico_noti_new.png") no-repeat; */
  background-size: 100% 100%;
}

.brdWrap.coaching .brdList .txt {
  margin-top: 1.2rem;
  padding-top: 1.2749rem;
  border-top: 0.1rem solid #eee;
}

.brdWrap.main {
  margin-top: 0;
  background: transparent;
}

.brdWrap.main .brdList .info {
  height: auto;
  padding-left: 0;
}

.brdWrap.main .brdList .info .tit {
  font-size: 1.6rem;
  text-overflow: initial;
  -webkit-line-clamp: initial;
}

.brdWrap.main .brdList .info .writer {
  margin-top: 0.5624rem;
}

.brdWrap.main .brdList .info .txt {
  margin-top: 1.2rem;
  padding-top: 1.2749rem;
  font-size: 1.4rem;
  line-height: 2.1rem;
  color: #222;
  border-top: 0.1rem solid #eee;
}

em[class*=cate] {
  display: inline-block;
  height: 2.4rem;
  margin-bottom: 0.8rem;
  padding: 0.2rem 1rem;
  border-radius: 9.9rem;
  font-size: 1.3rem;
  font-weight: 500;
  line-height: 1.95rem;
  letter-spacing: -0.026rem;
}

em.cate1 {
  background-color: #F2EFFF;
  color: #6539F8;
}

em.cate2 {
  background-color: #EFF4FF;
  color: #445FE6;
}

em.cate3 {
  background-color: #e5f6f8;
  color: #00a3bb;
}

em.cate4 {
  background-color: #fff6d3;
  color: #d88e00;
}

em.cate5 {
  background-color: #dbf2ec;
  color: #32a483;
}

em.cate6 {
  background-color: #f3f3fe;
  color: #b9a2f9;
}

em.cate7 {
  background-color: #e1e9fe;
  color: #445fe6;
}

em.cate8 {
  background-color: #ffeef0;
  color: #e55876;
}

em.cate9 {
  background-color: #f5d6eb;
  color: #d85ab1;
}

em.cate10 {
  background-color: #e1e4eb;
  color: #3d4466;
}

em.cateEtc {
  background-color: #eee;
  color: #666;
}

.brdWrap em.rec {
  background-color: #fceef1;
  color: #e55876;
}

.brdWrap em.end {
  background-color: #f1eefd;
  color: #5642bf;
}

.brdWrap .tit {
  display: block;
  font-size: 2.2rem;
  font-weight: 700;
  line-height: 2.86rem;
  letter-spacing: -0.044rem;
  color: #222;
  word-break: break-all;
}

.brdWrap i.lock {
  display: inline-block;
  width: 1.3rem;
  height: 1.6rem;
  margin-top: 0.3749rem;
  /* background: url("../../images/mobile/common/ico_lock.png") no-repeat top left/1.3rem auto; */
  vertical-align: top;
  font-size: 0;
  line-height: 0;
}

.brdWrap .writer {
  display: block;
  margin-top: 1.6rem;
  font-size: 1.6rem;
  font-weight: 600;
  line-height: 2.4rem;
  letter-spacing: -0.032rem;
  color: #222;
}

.brdWrap .txt {
  overflow: hidden;
  display: -webkit-box;
  margin-top: 1.2rem;
  font-size: 1.4rem;
  color: #666666;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
}

.brdWrap .detail {
  margin-top: 0.4rem;
  font-size: 0;
}

.brdWrap .detail span,
.brdWrap .detail a {
  display: inline-block;
  position: relative;
  margin-left: 0.8rem;
  padding-left: 1.2rem;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 2.1rem;
  letter-spacing: -0.028rem;
  color: #666;
  vertical-align: top;
}
.brdWrap .detail span:after,
.brdWrap .detail a:after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 0.4rem;
  height: 0.4rem;
  margin-top: -0.2rem;
  border-radius: 50%;
  background-color: #B8BBC4;
}
.brdWrap .detail span:first-child,
.brdWrap .detail a:first-child {
  margin-left: 0;
