.innerWrap {
  width: 100%;
  border-top-left-radius: 2rem;
  border-top-right-radius: 2rem;
  transform: translateY(100%);
  display: none;
  box-shadow: 0 0.2rem 0.7499rem rgba(0, 0, 0, 0.15);
  padding: 1.1249rem 1.1249rem 1.1249rem 1.1249rem !important;
}

.innerWrap.trans {
  display: block;
  position: relative;
  animation: bottoms 0.5s linear forwards;
}

@keyframes bottoms {
  from {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
    @keyframes bottoms {
      from {
        opacity: 0;
        transform: translate3d(0, 100%, 0);
        transform: translate3d(0, 100%, 0);
      }
      to {
        opacity: 1;
        transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
      }
    }
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
