.modal-background {
    display: flex;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.2);
  }

  /* TODO */
  .alert-background {
    /* background-color:rgb(255, 255, 255); */
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  .alert-title {
    border-radius: 20px 20px 0 0;
    background-color: #5D88FB;
    text-align: left;
    padding: 10px;
    padding-left: 30px;
    font-size: 2rem;
    font-weight: 700;
    line-height: 2.8rem;
    letter-spacing: -0.04rem;
    color: #eee;

  }
  .alert-content {
    position: relative;
    background-color: white;
    border-radius: 30px;
    width: 100%;
    height: 400px;
  }
  .alert-message {
    display: flex;
    align-items: center;
    width: 100%;
    height: 70%;
  }

  .alert-message p {
    width: 100%;
    text-align: center;
    font-size: 1.5rem;
    line-height: 2.8rem;
    letter-spacing: -0.04rem;
  }

  .confirm-button {
    position: absolute;
    display: flex;

    bottom: 0;
    left:0;
    right:0;
    border-radius: 5px;
    border-width: 2px;
    border-color: black;

  }

  .confirm-button  button {
    flex: 1;
    justify-content: center;
    align-items: center;
    text-align: center;
    border: none;
    padding: 10px;
    font-size: 2rem;
    line-height: 2.8rem;
    letter-spacing: -0.04rem;
    font-weight: 400;
    background-color: #5D88FB;
    color: #fff;
    border-radius: 0 0 20px 0px;

    &:first-child {
      background-color: #ccc;
      color: #646464;
      border-radius: 0 0 0 20px;

    }
  }

  .alert-button {
    position: absolute;
    display: flex;

    bottom: 0;
    left:0;
    right:0;
    border-radius: 5px;
    border-width: 2px;
    border-color: black;
